___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --magenta-escuro: #8b008b;
    --neve: #fffafa;
}

* {
    box-sizing: border-box;
    outline: 0;
}

body {
    margin: 0;
    padding: 0;
    background: var(--magenta-escuro);
    font-family: sans-serif;
    font-size: 1em;
    line-height: 1.5em;
}

.section {
    max-width: 560px;
    margin: 50px auto;
    background-color: var(--neve);
    border-radius: 20px;
    padding: 20px;
}


.section button {
    display: block;
    width: 100%;
    border: none;
    border-radius: 10px;
    background: var(--magenta-escuro);
    color: var(--neve);
    font-size: 18px;
    font-weight: bold;
    height: 40px;
    cursor: pointer;
}

.section button:hover {
    opacity: 80%;
}

.section .result {
    font-size: 45px;
    font-weight: bold;
    letter-spacing: 8px;
    padding: 30px 0;
    text-align: center;
}

